
.db-banner {
  position: relative;
  height: 7rem;
  background: url("../assets/banner-bg.jpg") no-repeat;
  background-size: cover;
}
.db-banner .db-banner-content {
    position: absolute;
    left: 1.8rem;
    top: 0;
    bottom: 0;
    right: 1rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.db-banner .db-banner-content .db-banner-title {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      line-height: 2.2rem;
      font-size: 1.4rem;
      color: #2CA532;
}
.db-banner .db-banner-content a {
      display: inline-block;
      width: auto;
      padding: 0 1.4rem;
      font-size: 1.3rem;
      font-weight: bold;
      line-height: 2;
      border: 0.1rem solid #42bd56;
      border-radius: 0.3rem;
}
.db-banner .db-banner-content .download {
      color: #fff;
      background: #42bd56;
}
.db-banner .db-banner-content .open {
      margin-left: 0.8rem;
      color: #42bd56;
}



