
.db-movie-content {
  padding-top: 1rem;
}
.db-movie-content .db-movie-classify h2 {
    padding: 0 1.6rem;
    height: 2.6rem;
    line-height: 2.6rem;
    font-weight: normal;
}
.db-movie-content .db-movie-classify ul {
    margin: 1.6rem 0 0 1.6rem;
}
.db-movie-content .db-movie-classify li {
    float: left;
    width: 50%;
    padding-right: 1.8rem;
    height: 4.2rem;
    line-height: 4.2rem;
    font-size: 1.6rem;
    border-top: 0.1rem solid #eee;
    border-right: 0.1rem solid #eee;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
.db-movie-content .db-movie-classify li a {
      color: #42bd56;
}
.db-movie-content .db-movie-classify li span {
      float: right;
      display: inline-block;
      width: 0.8rem;
      height: 0.8rem;
      margin-top: 1.5rem;
      border-right: solid 0.1rem #ccc;
      border-bottom: solid 0.1rem #ccc;
      font-weight: bold;
      color: #ccc;
      -webkit-transform: rotate(-45deg);
              transform: rotate(-45deg);
}
.db-movie-content .db-movie-classify li:nth-child(2n) {
    padding-left: 1.8rem;
}



