
.db-download-app {
  padding-bottom: 2rem;
  margin: 5rem 0 3rem 0;
  text-align: center;
  font-size: 1.5rem;
}
.db-download-app .db-download-info {
    display: inline-block;
    margin: 0 auto 1.5rem;
    font-size: 1.4rem;
    color: #111;
    text-align: left;
    overflow: hidden;
}
.db-download-app .db-download-info div {
      overflow: hidden;
}
.db-download-app .db-download-info strong {
      line-height: 2.8rem;
      font-size: 2.4rem;
      font-weight: normal;
}
.db-download-app .db-download-info .db-down-img {
      float: left;
      display: inline-block;
      width: 4.8rem;
      height: 4.8rem;
      margin-right: 1.2rem;
      background: url("../assets/douban-app-img.png") no-repeat;
      background-size: 4.8rem 4.8rem;
}
.db-download-app a {
    display: block;
    color: #42bd56;
}



